




















































import Vue from 'vue';
import {Component} from "vue-property-decorator";

@Component
export default class UserInfo extends Vue {
  data() {
    return {
      isLoading: false,
      userinfo: {}
    };
  }

  getData() {
    this.$axios.get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(res => {
            // @ts-ignore
            this.isLoading = false;
            // @ts-ignore
            this.userinfo = res.data.data;
        })
        .catch(function (err) {
          console.log('err');
          console.log(err);

        });
  }

  beforeMount() {
    // @ts-ignore
    this.isLoading = true;
    this.getData();
  }
}

